      '<div class="wpb_text_column wpb_content_element ">\n\\n<div class="wpb_wrapper">\n\\n\n\\nLargo is a specially designed gated sanctuary perfect for those perceptive people looking to host conferences, retreats, and even weddings in a unique and creative space. Largo is located just an hour?s drive from Miami International Airport, is easy to access, and offers many amenities. Being able to host around 100 guests the event space is divided into a dock area, a beach area, as well as indoor space. The rental of Largo for destination events includes exclusive use of the entire estate; private beach; infinity pool; 3 bedroom main lodge &amp; 6 guest bungalows (comfortably sleeping up to 15 single guests or 30 as couples); daily housekeeping and groundskeeper. To ensure privacy, admittance to Largo is by reservation or appointment only.\n\\n\n\\n</div>\n\\n</div>',
