    'Short description':
      'Miami Design Preservation League is a not-for-profit preservation and arts organization founded in 1976, which preserves, protects and promotes the architectural, cultural, social and environmental integrity of Miami Beach and the surrounding areas.\n\\n\n\\nOriginally organized by Barbara Baer Capitman and friends in 1976, it is the oldest Art Deco Society in the World.\n\\n\n\\nMDPL provides cultural and educational programs to Miami-Dade County residents, surrounding counties, citizens of Florida and to national and international visitors and tourists. Our programs are developed for the general public and have special appeal to those interested in art, design, architecture, history, preservation, urban and community planning and development.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
