    'Is featured?': 0,
    'Visibility in catalog': 'visible',
    'Short description':
      'All Bakehouse Resident Artists are selected by an independent jury of art professionals, such as curators, gallery directors, gallery owners and educators from institutions of higher learning. The Bakehouse provides affordable studio space, exhibition opportunities and advancement programming to our artists. Currently comprised of 60 individual artist studios, two galleries with monthly exhibition openings, a project room, classroom, print room, photo lab, woodworking shop, welding room, and ceramics kiln, the extensive facility offerings allow artists to discover, cross-over or integrate new media into their work. Soon-to-be-added work areas are a ceramics studio and South Florida\'s first community-accessible fine arts metal casting foundry made possible through a Knight Arts Challenge Grant from the John S. and James L. Knight Foundation. Resident artists receive notice of opportunities at the Bakehouse and elsewhere in South Florida such as art fairs, public artwork calls, workshops, grants, museum shows, special collaborations, and more.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
