    Name: 'Chesapeake Beach Resort',
    Published: 1,
    'Is featured?': 0,
    'Visibility in catalog': 'visible',
    'Short description':
      '<p style="font-weight: 300;">Otto Mears opened the original Chesapeake Beach Resort in 1900. It featured a boardwalk lined with concession stands, a roller coaster, a band shell, dancing and other forms of entertainment. The Belvedere Hotel was on the property for two decades until it was destroyed by fire, and the full resort facilities were damaged by hurricane in 1933. In 1946, the amusement park was refurbished and in 1948 slot machines were added. In 2004, Chesapeake Beach Resort and Spa was born, recapturing the original intent of the founders of this small town: to have a bayside resort just close enough to Washington, D.C., Baltimore, Annapolis and the surrounding area. An important piece of history lives on our grounds. The Chesapeake Beach Railway Museum celebrates the early part of the 20<sup>th</sup>centurya time when automobiles, mainly toys for the rich, and an active railway brought people to the peace and relaxation they sought.</p>\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
