    'Is featured?': 0,
    'Visibility in catalog': 'visible',
    'Short description':
      'The Fort Lauderdale Womans Clubwas founded onJanuary 11, 1911with 18 members by the Board of Trade. We were first called theWomans Civic Improvement Association. The Club predated the incorporation of the city itself by two and a half months. The Club was federated by the GFWC in 1912 and on November 28, 1913, the name of the club was changed to theFort Lauderdale Womans Club.Much of the history of the city is intertwined with the history of the Womans Club. In 1912 Club was instrumental in starting a volunteer fire department, donating $100 for their use. In 1913, club members got out the newspaper for vacationing publishers.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
