    'Is featured?': 0,
    'Visibility in catalog': 'visible',
    'Short description':
      'HistoryMiami Museum, a Smithsonian Affiliate, is the premier cultural institution committed to collecting, preserving and celebrating Miami\'s history as the unique crossroads of the Americas. Through exhibitions, artistic endeavors, city tours, education, research, collections and publications, HistoryMiami Museum works to help everyone understand the importance of the past in shaping Miami\'s future. HistoryMiami Museum connects people by telling the stories of Miami\'s communities, individuals, places and events. HistoryMiami Museum is a unique venue that can accommodate everything from intimate gatherings to large festivals at a range of different price-points. Our theater space can accommodate a lecture of up to 165, our mural gallery can host 275 for a cocktail party, and our plaza can host outdoor events of up to 4000. We connect to a parking garage via sky bridge with 400 spaces, and are able to validate parking for $5.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
