    Name: 'Ice Palace Films Studios',
    Published: 1,
    'Is featured?': 0,
    'Visibility in catalog': 'visible',
    'Short description':
      'Since 1923, Ice Palace Film Studios has stood on the corner of North Miami Avenue and NW 14th Street, its white fortress-like walls surrounded by a manicured garden ideal for lounging. Given that unique vibe, it\'s no shock the space has served as everything from the home of Pulse Art Fair during the week of Art Basel Miami Beach to a makeshift nightclub for Deadmau5\'s free show and events during Winter Music Conference. Because the immense space is flexible enough to fit almost any kind of request, it\'s also used frequently for film production and commercial photo shoots. In fact, it features 85,000 square feet of interior space, including the West Building, the former home of Karu &amp; Y and 58,000 square feet of garden area. The stripped-down interior means it can be decorated in any fashion imaginable. In fact, the most impressive transformation occurred in 2008, when Stoli created a faux hotel where invited guests could sip free cocktails and enjoy an array of live performances and DJ sets. Booking Ice Palace costs most than renting a Hialeah banquet hall, but when you don\'t want your vision compromised and you need space for a couple thousand people, it\'s money well spent.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
