    'Short description':
      'Located on Biscayne Bay in Coconut Grove, Florida, The Kampong contains a fascinating array of tropical fruit cultivars and flowering trees. The garden is named for the Malay or Javanese word for a village or cluster of houses. Some of the first plantings there came from Indo-Malaysia, brought there by the renowned explorer/horticulturist who introduced these species to the United States and who owned the property in the early 1900s. The collections were expanded and diversified by the next owner, who eventually gifted the garden to the NTBG.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
