    Name: 'The LAB Miami',
    Published: 1,
    'Is featured?': 0,
    'Visibility in catalog': 'visible',
    'Short description':
      'Of the 130+ companies already present, the LAB Miamis members range from freelancers, early-stage startups and nonprofits to corporations. By supporting creative, tech, and social enterprises we, as a community, will be able to transform Miami into a premier startup hub. We believe progress occurs when motivated &amp; determined individuals collide &amp; create new ideas. We believe entrepreneurs, startups and organizations need an open and inspiring place to gather, experiment, and foster connections that they otherwise wouldnt have found. Our members come from a wide range of disciplines and share a common desire to drive innovation. Becoming a LAB member is more than just working from a cool space. Its your opportunity to plug in to Miamis thriving entrepreneurial community. Whether youre a creative, techie, or traditional professional, we have one mission: toLearn, Act, and Build together.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
