    'Short description':
      'The location youve been searching for is finally here Miamis newest and most unique daylight studio. Meticulously created in a restored 1920s home with the convenience of an open floor plan. Beautiful natural light floods the white washed floored interior tastefully dressed with vintage French furniture, European antiques and a handcrafted kitchen. Molding on the bedroom walls flows in to the exquisite bathroom. The studio boasts perfect positioning for natural light all day making shooting at Stardust effortless. The quaint French style annex contains hair, make-up and wardrobe facilities. The exterior expands over two lots and offers a wrap around veranda adorned with an outdoor claw foot tub, French country furnishings and window shutters. The huge wooden weathered deck area has an arbor built on to the annex with removable railings. The lush, tropical garden has meandering paths, hammocks and tree swings wrapped in a picket fence. A 1965 Chevy pickup sits on the sand-like gravel making Shooting Stardust Studio the most versatile and convenient location for all your stills, video and event productions. Ample parking is available.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
