    'Short description':
      'The Wynwood Walls was conceived by the renowned community revitalizer and placemaker, the late Tony Goldman in 2009. He was looking for something big to transform the warehouse district of Wynwood, and he arrived at a simple idea: Wynwoods large stock of warehouse buildings, all with no windows, would be my giant canvases to bring to them the greatest street art ever seen in one place. Starting with the 25th26th Street complex of six separate buildings, his goal was to create a center where people could gravitate to and explore, and to develop the areas pedestrian potential.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
