      'Douglas Entrance Office Parc, the only business park in Coral Gables and a city landmark, enjoys a 6.1 acre lushly landscaped setting offering a unique office campus environment. It is situated at the intersection of SW 37th Avenue, also known as Douglas Road, and SW 8th Street. These two roads afford the property multiple ingress/egress routes and convenient access to the region\'s most important transportation infrastructure and business centers, including Brickell and Downtown (4.0 miles east), Miami International Airport (2.5 miles north), Dolphin Expressway (2.0 miles north), US-1 (2.2 miles south) and Miami Beach (9.0 miles east). From the iconic entry arch and bell tower to the lushly tropical landscaping and expansive courtyard, Douglas Entrance Office Parc is a premier Class A business campus offering everything a company could need in one location.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
