    'Short description':
      'Old School Square Cultural Arts Center is a historic area located at 51 North Swinton Avenue in Delray Beach, Florida, United States. It is built on the former site and former buildings of the now defunct Delray Elementary School and Delray High School, at the corner of Atlantic Avenue and Swinton Avenue, anchoring Delray\'s historic downtown shopping district. As of 2006, Old School Square comprises the Cornell Museum of Art &amp; American Culture, the Crest Theatre, a refurbished vintage gymnasium, and an outdoor entertainment pavilion.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
