      'If you had visited Coral Castle in the 1940s you would have been greeted enthusiastically by a man weighing a mere 100 pounds and standing just over 5 feet tall. He would have asked you for ten cents admission and introduced you to his fantasy world. As you moved around his sculpture garden in stone, and the significance of each piece was explained, you would have been witness to the great pride Ed Leedskalnin took in his work.Since it is documented that no one ever witnessed Eds labor in building his beloved Coral Castle, some have said he had supernatural powers. Ed would only say thathe knew the secrets used to build the ancient pyramids and if he could learn them, you could too.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
