      'Crandon Park was a donation, made to the County, in 1940 by heirs of Commodore William John Matheson, a wealthy aniline dye businessman who used the land as a coconut plantation, with the condition that the land be used as a public park. In exchange for the land donation Chairman Charles H. Crandon of the County Commission, for whom the park is named, offered to have the County build a causeway that would connect Key Biscayne to the mainland.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
