    'Short description':
      'History Fort Lauderdale, formerly Fort Lauderdale Historical Society, believes that a sense of history is fundamental to understanding human experience, and therefore, collects preserves and shares material from our community\'s past, so that present and future generations can comprehend more fully their predecessors, their community and themselves. With education as the primary focus, we offer public lectures and workshops; publish teacher resource materials; arrange school and general group tours and activities; support scholarly research through significant research assistance; maintain a 1907 house museum and three other 1905 historic structures, and a museum of changing and permanent exhibitions, and operate a research center that has functioned uninterrupted for fifty one years.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
