    'Short description':
      'Located in the heart of downtown West Palm and catered by the Award-Winning Hilton West Palm Beach, the Harriet Himmel Theater is the perfect location to host your next event. The Theaters beautiful hardwood floors, grand archways, and vaulted ceilings provide the perfect backdrop to capture your guests attention and create unique and memorable event experiences. Stage your ideal wedding in one of the finest examples of historical architecture in a unique, spectacular space, with an award-winning team to help create the wedding of your dreams. Built in 1926 and redesigned as an event venue in 2000, it can accommodate 200 guests for a seated dinner, 500 theater-style, and 400 for receptions.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
