    'Short description':
      'Designed by internationally acclaimed yacht designer Luis De Basto, our 222 ft., 2800 international tons $40M purpose-built vessel is the largest commercial operation ship in the Inter coastal waterway. This uber-luxe mega yacht offers the ultimate excursion experience where sea and sky converge with iconic Miami\'s unique glamour. Its multi-faceted event spaces make the one of the most sought-after event venues available. Our Le Cordon Blue-trained catering team will delight every palette with a unique, customized menu to maximize the ultimate epicurean experience for the guests. From chick passed hors devours for an informal meeting, to elaborate luxurious Continental cuisine for a black-tie gala; the meal will prove as unforgettable as the event itself.With over 25 years of event experience, SeaFair\'s extremely talented team provides clients with on-going support throughout the even-planning process. Exceeding their expectations by making their unique visions into one-of-a-kind realities. Design and decor are the two basic elements of a perfectly orchestrated event. Our design event team excels at helping clients make legendary statements and develop innovative ideas for any occasion. Working hand-in-hand with them, our floral design team creates breathtaking arrangements that compliment and enhance SeaFair\'s elegant decor.\n\\n\n\\n[vc_empty_space height="30px"][vc_btn title="Request Now" style="flat" shape="square" color="chino" size="lg" link="url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|"]',
